.App{
  height: 100%;
  width: 100%;
  display: grid;
  grid-template-rows: repeat(12,1fr);
  grid-template-columns: repeat(12,1fr);
  background-color: skyblue;
}
.App > .Header{
  
  width: 100%;
  height: min-content;
  background-color: #00AD7C;
  border: 3px solid #0B88A8;
  grid-column: 12 span ;
  
}
.App>Manu{
  background-color: #164753;
  border: 3px solid #F9B208;
  grid-column: 1 span;
  
}
.App>.Main{
  grid-column: 11 span;
  border: 3px solid #F9B208;
  background-color: black;
  grid-row: 10 span;
  overflow-y: auto;

}
.App>.Footer{
  position:absolute;
  bottom:0;
  width:100%;
  height:70px;
  background-color: #00AD7C;

}
.App>.Footer:hover{
  color: rgb(215, 233, 24);
}