.ToDoItem {
	display: inline-block;
  z-index: 1;
}
.flip-card {
    background-color: transparent;
    width: 300px;
    height: 300px;
    perspective: 1000px;
   
  }
  
  .flip-card-inner {
    position: relative;
    width: 100%;
    height: 100%;
    text-align: center;
    transition: transform 0.6s;
    transform-style: preserve-3d;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
  }
  
  .flip-card:hover .flip-card-inner {
    transform: rotateY(180deg);
  }
  
  .flip-card-front, .flip-card-back {
    position: absolute;
    width: 100%;
    height: 100%;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
  }
  
  .flip-card-front {
    background-color: #2980b9;
    color: black;
  }
  
  .flip-card-back {

    background-color: #2980b9;
    color: white;
    transform: rotateY(180deg);
    z-index: 2;
  }
  .ToDoItem img{
    width:300px;
    height:300px;
  }
  .flip-card-front > h1{
    position: absolute;
    z-index: 1;
  }
  .flip-card button{
    border: none;
    outline: 0;
    padding: 7px;
    color: white;
    background-color: #2474a9;
    text-align: center;
    cursor: pointer;
    width: 100%;
    font-size: 18px;


  }
  .flip-card button:hover{
  
    color: yellow;
  }